import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Empresa } from './../../../model/objetc/empresa';
import { EmpresaService } from './../../../service/sigobra/empresa.service';
import { Base } from '../../../model/base';
import { Estado } from '../../../model/enums/estado.enum';
import { ViacepService } from '../../../service/viacep/viacep.service';

@Component({
  selector: 'app-empresas',
  templateUrl: './empresas.page.html',
  styleUrls: ['./empresas.page.scss'],
})
export class EmpresasPage implements OnInit {

  empresas: Array<Empresa>;
  empresa: Empresa;
  value: string = '';
  constructor(private base: Base, private es: EmpresaService,
    private vcs: ViacepService,
    private router: Router) { }

  ngOnInit() {
      
  }

  ionViewWillEnter() {
    this.carregarEnpresa(" ");    
  }

  excluir(empresa : Empresa) {
    this.base.Confirma('Deseja excluir essa empresa', this.removerativo.bind(this), empresa);
  }

  remover(empresa: Empresa) {
    this.base.present();   
    return this.es.delete(empresa).subscribe(
      data => {
        this.base.dismiss();
        this.base.menssagemSucesso('Empresa Excluida');
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  empresa '+ empresa.id + this.base.tratarErro(error));  
      }
    );
  }



  editar(empresa: Empresa) { 
    this.es.empresa = empresa;
    this.router.navigate(['empresa-inserir']);   
  }

  carregarEnpresa(value: string) {
    this.base.present();
    return this.es.pesquisa(value).subscribe(
      data => {
        this.empresas = data as Array<Empresa>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  empresa ' + this.base.tratarErro(error));
      }
    );
  }
  
  
  carregarEnpresas(value: string) {
    this.base.present();
    return this.es.pesquisar(value).subscribe(
      data => {
        this.empresas = data as Array<Empresa>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  empresa ' + this.base.tratarErro(error));
      }
    );
  }

  inserir() {
    this.es.empresa = new Empresa;
    this.router.navigate(['empresa-inserir']);
  }

  removerativo(empresa: Empresa) {
    this.empresa= empresa;
    this.base.present();  
    if ( (this.empresa.ativo !== null)&& (this.empresa.ativo !== undefined)){      
      this.empresa.ativo = false;
    }  
    return this.es.editarativo(this.empresa).subscribe(
      data => {        
        this.base.dismiss();
        this.router.navigate(['empresas']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  empresa '+ this.empresa.id + this.empresa.ativo 
        + this.base.tratarErro(error));  
      }
    );           
  }

  consultaCEP(cep) {

    if (cep !== null && cep !== undefined && cep !== '') {
      cep = cep.replace(/\D/g, '');
      if (cep.length > 7) {
        this.base.present();
        this.vcs.consultaCEP(cep).subscribe(
          data => {
            this.base.dismiss();
            this.popula(data);
          }, error => {
            this.base.dismiss();
            this.base.mensagemErro('Falha ao buscar CEP :' + this.base.tratarErro(error));
          }
        );
      }
    }
  }

  popula(data) {
    if (data.erro !== undefined && data.erro !== null && data.erro) {
      this.base.mensagemErro('Falha ao consultar endereço do CEP');
      return;
    }
    this.empresa.logradouro = data.logradouro;
    this.empresa.complemento = data.complemento;
    this.empresa.bairro = data.bairro;
    this.empresa.cidade = data.localidade;
    this.empresa.uf = data.uf;
    for (var uf in Estado) {
      if (data.uf === uf) {
        this.empresa.estado = Estado[uf];
        break;
      }
    }
  }
}
